html,
body {
  margin: 0;
  padding: 0;
}

html {
  /* margin-right: calc(-1 * (100vw - 100%)); */
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  overflow-x: hidden;
  margin: 0;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  font-size: 16px;
  line-height: 24px;
